<template>
  <div class="all">

    <div class="value">

    </div>

    <div class="translation">
      <el-input
        type="textarea"
        :rows="2"
        placeholder="请输入单词或者句子"
        v-model="textarea"
        size="medium"
        id="value"
        ref="value">
      </el-input>

    </div>

    <p v-if="this.success">{{this.word}}  录入成功</p>
    <el-button type="primary"
      plain id="btn3"
      @click="addWord">录入
    </el-button>

  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'AddWords',
  data() {
    return {
      textarea: '',
      success:false,
      word:''
    }
  },

  methods: {
    addWord() {
      let regx = /^[A-Za-z\x20]+$/

      if (this.textarea == '' || this.textarea == null) {
        alert('请输入内容')
        this.textarea = ''
      } else {
        if (!regx.test(this.textarea)) {
          alert('请输入单词')
          this.textarea = ''
        } else {
          axios({
            method: 'post',
            url: 'http://121.40.113.226:7778/word/addWord',
            data: {
              uid: this.$store.state.uid,
              value: this.textarea,
            },
          }).then((res) => {
            // console.log(res)
            this.data = res.data.data
            this.success=true
            this.word=this.textarea
            this.textarea = ''
            
          })
        }
      }
    },
  },
  mounted() {
    if (
      this.$store.state.uid == null ||
      this.$store.state.uid == '' ||
      this.$store.state.uid === undefined
    ) {
      alert('请先登录')
      this.$router.push('/login')
    }
  },
}
</script>
<style>
#btn3 {
  position: absolute;
  width: 200px;
  top:200px;
  left: 95px;
  height: 60px;
  font-size: 1.5em;
}

#el-input {
  font-size: 200px;
}
</style>
